.header {
  background-image: url("../image/header-bg.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover; }

.nav {
  position: relative; }
  .nav__item-position {
    display: flex;
    justify-content: flex-start;
    /* li::after {
                position: absolute;
                content: "";
                bottom: 30%;
                width: 0;
                height: 2px;
                background: $color_yelow;
                transition: all ease 0.4s;
            }
            li:hover:after {
                width: 130px;
            } */ }
    .nav__item-position li {
      width: 130px;
      height: 100px;
      display: flex;
      justify-content: center;
      align-items: center;
      border-bottom: 1px solid #E5E5E5; }
      .nav__item-position li a {
        position: relative;
        font-size: 12px;
        color: #FFFFFF; }
      .nav__item-position li a::before,
      .nav__item-position li a::after {
        position: absolute;
        content: "";
        bottom: -20%;
        left: -10%;
        transition: all ease 0.4s;
        width: 0;
        height: 0;
        background: #E8C300; }
      .nav__item-position li a:hover:before {
        width: 2px;
        height: 100%; }
      .nav__item-position li a:hover:after {
        width: 100%;
        height: 2px; }
    .nav__item-position li:nth-child(5) {
      background: black;
      margin-left: 30%; }
  .nav__logo {
    position: absolute;
    top: 25px;
    left: 50%;
    transform: translateX(-50%); }

